/*----------  Shadows  ----------*/
.shadow-1 {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 2px 7px !important;
}

.shadow-2 {
  box-shadow: rgba(0, 0, 0, 0.15) 0px 9px 30px !important;
}

.shadow-3 {
  box-shadow: rgba(0, 0, 0, 0.48) 0px 2px 10px !important;
}

.divider {
  display: inline-block;
  width: 100%;
  clear: both;
  height: 1px;
  background: #9ec8de;
  margin: 40px 0;
}

img:not(.is-original) {
  max-width: 100%;
  height: auto;
  display: inline-block;
}

.image-rounded {
  width: 175px;
  height: 175px;
  border-radius: 100%;
  overflow: hidden;
  margin-top: 15px;
  margin-bottom: 15px;
  position: relative;
}

.image-rounded img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 110%;
  max-height: 110%;
  width: auto;
  height: auto;
}

.image-rounded.image-outline {
  border-radius: 100%;
  overflow: inherit;
}

.image-rounded.image-outline:before {
  border-radius: 100%;
}

.image-rounded.image-outline > * {
  display: block;
  overflow: hidden;
  border-radius: 100%;
}

.image-outline {
  position: relative;
  display: block;
}

.image-outline:before {
  content: "";
  display: block;
  width: calc(100% + 18px);
  height: calc(100% + 18px);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid #005388;
}

.text-center .image-rounded, .text-center .image-outline, .center .image-rounded, .center .image-outline {
  margin-left: auto;
  margin-right: auto;
}

.img-cover {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  min-width: 100%;
  min-height: 100%;
  max-width: inherit;
}

code, .code {
  padding: 5px;
  background: #F3F8FB;
  color: #ce6363;
  display: inline-block;
  width: auto;
}

.bg-cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: 1;
}

.bg-cover ~ * {
  position: relative;
  z-index: 2;
}

.link-phone {
  text-decoration: none;
}

.link-phone a, .link-phone a:hover, .link-phone a:active, .link-phone a:visited {
  text-decoration: none;
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
}

.block-color {
  margin-bottom: 30px;
}

.block-color__title {
  font-size: 16px;
  margin-top: 0;
  margin-bottom: 10px;
  color: #005388;
}

.block-color__figure {
  max-width: 50px;
  min-width: 50px;
  height: 50px;
  position: relative;
  border-radius: 50%;
  overflow: hidden;
  display: inline-block;
  vertical-align: middle;
}

.block-color__content {
  padding: 5px 15px;
  display: inline-block;
  vertical-align: middle;
}

.block-color__content ul {
  margin-bottom: 0;
}

.block-color__content li {
  padding: 0px 10px;
  margin: 3px 0;
  background: #f5f5f5;
  color: #222;
  font-size: 12px;
}

.block-color__content li:before {
  content: none;
}

.heading + .box {
  margin-top: -15px;
}

ul.row li {
  margin-bottom: 0;
}

ul.row li i.icon-large {
  font-size: 28px;
}

ul.row li:before {
  content: none;
}

